.btn {
    // transition: color .3s ease, border .3s ease, box-shadow .3s ease;
    @include btn;

    &:not(.btn-lg):not(.btn-sm):not(.btn-xs) {
        @include btn-md;
    }
}

// Parameter: btn-color($textColor, $textColorHover, $textColorFocus, $bgColor, $bgColorHover, $bgColorFocus)
.btn-primary {
    @include btn-color(var(--fg-inverse), var(--fg-inverse), var(--fg-inverse), var(--bg-brand), var(--bg-brand-hover), var(--bg-brand-pressed));
}

.btn-secondary {
    @include btn-color(var(--fg-brand), var(--fg-inverse), var(--fg-inverse), var(--bg-secondary), var(--bg-secondary-hover), var(--bg-secondary-pressed));
}

.btn-default {
    @include btn-color(var(--fg-main), var(--fg-main), var(--fg-main), var(--bg-neutral), var(--bg-neutral-hover), var(--bg-neutral-pressed));
}

.btn-ghost {
    @include btn-color(var(--fg-main), var(--fg-main), var(--fg-main), transparent, var(--bg-neutral-hover), var(--bg-neutral-pressed));
}

.btn-ghost-brand {
    @include btn-color(var(--fg-brand), var(--fg-brand), var(--fg-brand-strong), var(--bg-ghost-brand), var(--bg-ghost-brand-hover), var(--bg-ghost-brand-pressed));
}

.btn-outline {
    @include btn-color(var(--fg-main), var(--fg-main), var(--fg-main), white, var(--bg-ghost-neutral-hover), var(--bg-ghost-neutral-pressed));
    border: 1px solid var(--border-light);
    box-shadow: var(--shadow-50);

    &:hover {
        border: 1px solid var(--border-light);
    }

    &:focus,
    &:active,
    &.active {
        border: 1px solid var(--border-light);
    }
}

.btn-info {
    @include btn-color(var(--fg-inverse), var(--fg-inverse), var(--fg-inverse), var(--bg-accent-info), var(--bg-accent-info-hover), var(--bg-accent-info-pressed));
}

.btn-success {
    @include btn-color(var(--fg-inverse), var(--fg-inverse), var(--fg-inverse), var(--bg-accent-success), var(--bg-accent-success-hover), var(--bg-accent-success-pressed));
}

.btn-warning {
    @include btn-color(var(--fg-inverse), var(--fg-inverse), var(--fg-inverse), var(--bg-accent-warning), var(--bg-accent-warning-hover), var(--bg-accent-warning-pressed));
}

.btn-danger {
    @include btn-color(var(--fg-inverse), var(--fg-inverse), var(--fg-inverse), var(--bg-accent-danger), var(--bg-accent-danger-hover), var(--bg-accent-danger-pressed));
}